// 
import {
  brand
}from '../../data/data.js'
export default function PfbCtrl($http) {
  var vm = this;
  vm.data = {};
  vm.brand = brand;
  vm.submitText = '';
  vm.isDisabled = false;
  function fail() {
     vm.isSubmitSuccess = false;
     vm.submitText = '';
     vm.isDisabled = false;
  }
  
  
  vm.showModal = false;
  vm.submit = function() {
    var para = $.extend({},vm.data);
    para.type = vm.data.type.id
    vm.submitText = '';
    $http.post('/CysjFb/CyFb',para).success(function(d) {
      if(d.success) {
        vm.isSubmitSuccess = true;
      }else {
        vm.errorMsg = ",,";
        fail();
      }
      vm.showModal = true;
    }).error(function() {
      fail();
      vm.showModal = true;
    })
  }
  
   
   vm.selectTrans = function (event, transNum) {
    vm.data.transaction = transNum;
    $(event.target).parents('.formSet').find('.formInputSet_radio').css({
      "background-position": "0 0"
    })
    $(event.target).css({
      "background-position": "0 -20px"
    });
  };
  
  // 
  $http.post("/Zbfb/Get").success(function (data) {
    vm.realnameauth = data.result.realnameauth;
  });
}


